<template>
    <!-- <div class="playPage" :style="`background-image:url(${song.picUrl})`"> -->
    <div class="playPage">
        <div v-if="song.picUrl" class="mask-pc"
            :style="`background-image:url(${song.picUrl}?imageView=1&type=webp&thumbnail=375x0;filter:blur(15rem)`">
        </div>
        <div v-else class="mask-pc" :style="`background-image:url(${song.al.picUrl});filter:blur(15rem)`"></div>
        <header v-if="song.song">
            <i class="fa fa-angle-left" aria-hidden="true" @click="$parent.showPlayPage = false"></i>
            <div class="name">
                <h3>{{song?.name}}</h3>
                <span>{{song.song.artists[0].name}} {{song.song.artists[1] ? '/ ' + song.song.artists[1].name :
                ''}}</span>
            </div>
            <i class="fa fa-share-alt" aria-hidden="true" @click="isShare=!isShare"></i>
        </header>
        <!-- song 里的属性不一样，需要另外判断 -->
        <header v-else>
            <i class="fa fa-angle-left" aria-hidden="true" @click="$parent.showPlayPage = false"></i>
            <div class="name">
                <h3>{{song?.name}}</h3>
                <span>{{song.ar[0].name}} {{song.ar[1] ? '/ ' + song.ar[1].name :
                ''}}</span>
            </div>
            <i class="fa fa-share-alt" aria-hidden="true" @click="isShare=!isShare"></i>
        </header>
        <section>
            <PlayDisco v-if="showDisc" @click.native="showDisc = false" :song="song" :playing="playing"
                :songLyric="songLyric" :tmpCurrentTime="tmpCurrentTime" />
            <PlayLyric v-else @click.native="showDisc = true" :playing="playing" :songLyric="songLyric"
                :tmpCurrentTime="tmpCurrentTime" />
        </section>
        <transition name="ani-share">
            <section class="share" v-show="isShare">
                <h3>分享到</h3>
                <span class="close-share" @click="isShare=false"></span>
                <div class="shareTo">
                    <div class="weixin">
                        <i></i>
                        <span>微信</span>
                    </div>
                    <div class="pengyouquan">
                        <i></i>
                        <span>朋友圈</span>
                    </div>
                    <div class="qq">
                        <i></i>
                        <span>QQ</span>
                    </div>
                    <div class="qqkongjian">
                        <i></i>
                        <span>QQ空间</span>
                    </div>
                    <div class="weibo">
                        <i></i>
                        <span>微博</span>
                    </div>
                </div>
            </section>
        </transition>
        <footer>
            <div class="top-btn">
                <div class="volume">
                    <i class="fa fa-volume-up" aria-hidden="true" @click="isVolume=!isVolume" v-show="!mute"></i>
                    <input class="inp-volume" type="range" min="0" max="100" :value="sound*100" @input="draging = true"
                        @change="draging = false;
                        $emit('update-volume', ($event.target.value/100));">
                    <transition name="ani-volume">
                        <div class="v-mask" v-show="isVolume">
                            <div class="sound" :style="`height:${sound*100}rem;`"></div>
                        </div>
                    </transition>
                    <span class="mute" v-show="mute" @click="isVolume=!isVolume"></span>
                </div>
                <i class="fa fa-commenting-o" aria-hidden="true"></i>
                <i class="fa fa-caret-square-o-right" aria-hidden="true"></i>
                <i class="fa fa-bell-o" aria-hidden="true"></i>
                <i class="fa fa-ellipsis-v" aria-hidden="true"></i>
            </div>
            <div class="time-bar">
                <span>
                    {{parseInt(tmpCurrentTime/60%60)>9?parseInt(tmpCurrentTime/60%60):'0'+parseInt(tmpCurrentTime/60%60).toFixed(0)}}
                    :
                    {{parseInt(tmpCurrentTime%60)>9?parseInt(tmpCurrentTime%60):'0'+parseInt(tmpCurrentTime%60)}}
                </span>
                <div class="bar">
                    <input class="range" type="range" min="0" :max="duration" :value="tmpCurrentTime"
                        @input="draging = true" @change="draging = false;
                        $emit('update-time', $event.target.value);">
                    <div class="mask" :style="`width:${(200/duration)*tmpCurrentTime}rem`"></div>
                </div>
                <span>
                    {{parseInt(duration/60%60)>9?parseInt(duration/60%60):'0'+parseInt(duration/60%60)}} :
                    {{parseInt(duration%60)>9?parseInt(duration%60):'0'+parseInt(duration%60)}}
                </span>
            </div>
            <div class="play-btn">
                <div class="btn-type">
                    <span class="sxbf" :title="models[0]" v-if="model===models[0]"
                        @click="$emit('change-model');count++"></span>
                    <span class="suiji" :title="model[1]" v-if="model===models[1]"
                        @click="$emit('change-model');count++"></span>
                    <span class="dqxh" :title="model[2]" v-if="model===models[2]"
                        @click="$emit('change-model');count++"></span>
                    <transition>
                        <div class="model-tips" v-if="model===models[0]&&count>1">{{models[0]}}</div>
                    </transition>
                    <transition>
                        <div class="model-tips" v-if="model===models[1]">{{models[1]}}</div>
                    </transition>
                    <transition>
                        <div class="model-tips" v-if="model===models[2]">{{models[2]}}</div>
                    </transition>
                </div>
                <i class="fa fa-step-backward" aria-hidden="true" @click="$emit('change-song', 'prev')"></i>
                <div class="isPlaying">
                    <i class="fa fa-play" aria-hidden="true" v-if="!playing" @click.stop="$emit('song-play')"></i>
                    <i class="fa fa-pause" aria-hidden="true" v-else @click.stop="$emit('song-pause')"></i>
                </div>
                <i class="fa fa-step-forward" aria-hidden="true" @click="$emit('change-song', 'next')"></i>
                <i class="fa fa-bars" aria-hidden="true" @click.stop="$emit('toggle-showSongList')"></i>
            </div>
        </footer>
    </div>
</template>

<script>
import PlayDisco from "@/components/PlayDisco.vue";
import PlayLyric from "@/components/PlayLyric.vue";
export default {
    components: {
        PlayDisco,
        PlayLyric,
    },
    props: {
        song: Object,
        playing: Boolean,
        duration: Number,
        currentTime: Number,
        model: String,
        songIds: Number,
        models: Array,
        volume: [Number, String],
    },
    data() {
        return {
            showDisc: true,
            draging: false,
            tmpCurrentTime: this.currentTime,
            str: "",
            songLyric: [],
            groups: null,
            show: false,
            sound: this.volume,
            isVolume: false,
            count: 0,
            mute: false,
            isShare: false
        };
    },
    methods: {
        getLyric: function (id) {
            this.axios.get("https://apis.netstart.cn/music/lyric", { params: { id } })
                .then((res) => {
                    // console.log(res.data.lrc.lyric);
                    this.str = res.data.lrc.lyric.split("\n");
                    this.songLyric = this.str.filter(o => o).map(o => {
                        // console.log(o);
                        this.groups = o.match(
                            /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
                        );
                        // console.log(this.groups);
                        if (this.groups) {
                            return {
                                time: this.groups.groups.mm * 60 + this.groups.groups.ss * 1,
                                text: this.groups.groups.text.trim(),
                            };
                        }
                    });
                    // console.log(this.songLyric);
                });
        },
    },
    watch: {
        currentTime: function (n) {
            if (!this.draging) {
                this.tmpCurrentTime = n;
            }
        },

        songIds: function (nId) {
            // console.log(nId);
            // this.songLyric = [];
            this.getLyric(nId);
        },
        volume: function (n) {
            if (!this.draging) {
                this.sound = n;
                // console.log(n);
            }
            if (n === 0) {
                this.mute = true;
            } else {
                this.mute = false;
            }
        },
    },
    created() {
        this.getLyric(this.song.id);
    }
};
</script>

<style lang="less" scoped>
@keyframes switch {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes volume {
    from {
        height: 0;
    }

    to {
        height: 100rem;
    }
}

@keyframes share {
    from {
        transform: translateY(200rem);
    }

    to {
        transform: translateY(0rem);
    }
}

.ani-volume-enter-active {
    animation: volume .1s linear;
}

.ani-volume-leave-active {
    animation: volume .1s linear reverse;
}

.ani-share-enter-active {
    animation: share .2s linear;
}

.ani-share-leave-active {
    animation: share .2s linear reverse;
}

.playPage {
    width: 100%;
    height: 100vh;
    background-color: rgb(75, 74, 74);
    background-size: cover;
    background-position: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: white;

    .mask-pc {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        background-size: cover;
        background-position: center;
        transform: scale(1.5);

        &::before {
            content: " ";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.619);
        }
    }

    header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10rem;

        .fa-angle-left {
            font-size: 30rem;
            margin-left: 10rem;
        }

        .fa-share-alt {
            font-size: 22rem;
            margin-right: 10rem;
        }

        .name {
            width: 200rem;
            text-align: center;

            h3 {
                width: 200rem;
                font-size: 18rem;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                word-break: normal;
            }

            span {
                color: #999;
                display: block;
                width: 200rem;
            }
        }
    }

    footer {
        margin-bottom: 40rem;

        .top-btn {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin: 40rem;
            margin-bottom: 20rem;
            margin-top: 0;
            font-size: 18rem;

            .volume {
                width: 15rem;
                height: 20rem;
                position: relative;

                .inp-volume {
                    position: absolute;
                    bottom: 65rem;
                    left: -42rem;
                    width: 100rem;
                    transform: rotateZ(270deg);
                    opacity: 0;
                    z-index: 999;
                }

                .v-mask {
                    position: absolute;
                    bottom: 25rem;
                    left: 5rem;
                    width: 6rem;
                    height: 100rem;
                    background-color: #999;
                    transform: rotateX(180deg);
                    border-radius: 5rem;

                    .sound {
                        width: 100%;
                        height: 0;
                        background-color: white;
                        border-radius: 5rem;
                    }
                }

                .mute {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 20rem;
                    height: 20rem;
                    background-image: url(../assets/mute.png);
                    background-size: cover;
                }
            }

            .fa-caret-square-o-right {
                margin-right: 2rem;
            }

            .fa-bell-o {
                margin-right: 4rem;
            }

            .fa-ellipsis-v {
                margin-right: 4rem;
            }



        }

        .time-bar {
            display: flex;
            justify-content: center;
            align-items: center;

            .bar {
                width: 200rem;
                height: 4rem;
                // background-color: red;
                border-radius: 10rem;
                margin: 0 20rem;
                position: relative;
                background-color: gray;

                .range {
                    width: 100%;
                    height: 200%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    opacity: 0;
                }

                .mask {
                    width: 0;
                    height: 100%;
                    background-color: white;
                    border-radius: inherit;
                    position: absolute;
                    top: 0;
                    left: 0;
                }
            }
        }

        .play-btn {
            margin: 10rem;
            display: flex;
            justify-content: space-around;
            align-items: center;
            font-size: 16rem;

            button {
                border: none;
                outline: none;
                background: transparent;
                color: white;
            }

            .isPlaying {
                width: 15rem;
                height: 15rem;
                position: relative;

                i {
                    position: absolute;
                    top: 0rem;
                    left: 0rem;
                }
            }
        }

        .btn-type {
            position: relative;
            width: 22rem;
            height: 22rem;

            span {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-size: cover;

                &.sxbf {
                    background-image: url(../assets/sxbf.png);
                }

                &.dqxh {
                    background-image: url(../assets/dqxh.png);
                }

                &.suiji {
                    background-image: url(../assets/sjbf.png);
                }
            }

            .model-tips {
                position: absolute;
                top: -100rem;
                left: 100rem;
                width: 100rem;
                height: 30rem;
                line-height: 30rem;
                text-align: center;
                color: rgb(212, 208, 208);
                font-size: 12rem;
                background-color: rgba(0, 0, 0, 0.511);
                border-radius: 15rem;
                opacity: 0;
                animation: switch 1s linear;
            }
        }
    }
}

.share {
    width: 100%;
    height: 150rem;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: white;
    color: black;
    z-index: 99;
    padding: 10rem;

    h3 {
        font-size: 18rem;

    }

    .close-share {
        position: absolute;
        top: -40rem;
        right: 10rem;
        width: 30rem;
        height: 30rem;
        background-image: url(../assets/close.png);
        background-size: cover;
    }

    .shareTo {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        &>div {
            width: 60rem;
            height: 60rem;
            text-align: center;
            margin-top: 20rem;

            span {
                font-size: 16rem;
                display: block;
                margin-top: 4rem;
            }

            i {
                display: block;
                width: 32rem;
                height: 32rem;
                margin-left: 15rem;
                background-repeat: no-repeat;
            }
        }

        .weixin {
            i {
                background-image: url(../assets/weixin.png);
            }
        }

        .pengyouquan {
            i {
                background-image: url(../assets/pengyouquan.png);
            }
        }

        .qq {
            span {
                margin-top: 6rem;
            }

            i {
                background-image: url(../assets/icon_QQ.png);
            }
        }

        .qqkongjian {
            i {
                background-image: url(../assets/QQkongjian.png);
            }
        }

        .weibo {
            i {
                background-image: url(../assets/weibo.png);
            }
        }
    }
}
</style>
