    <h3>编辑推荐</h3>
    <ul class="card-list">
      <HomeCard v-for="item in personalizeds" :key="item.id" :item="item"></HomeCard>
    </ul>

    <h3>最新音乐</h3>
    <ul>
      <SongItem v-for="song in newsongs" :key="song.id" :song="song" @update-song="$emit('update-song', $event)"
        :songId="songId" :playing="playing" 
        @click.native="$emit('update-song-list', newsongs)"/>
    </ul>
    <!-- loading -->
    <div class="loading" v-if="loading"></div>
    <footer class="home-ft">
      <div class="ftwrap">
        <div class="logo-ft">
          <i></i>
          <span>网易云音乐</span>
