<template>
    <div class="playDisco">
        <div class="needle" :class="{ndTo: !playing}"></div>
        <div class="turn">
            <div class="song-rollwrap" :class="{playing: playing}">
                <div class="songImg">
                    <img v-if="song.picUrl" :src="`${song.picUrl}?imageView=1&type=webp&thumbnail=184x0`" alt="" />
                    <img v-if="song.al" :src="`${song.al.picUrl}?imageView=1&type=webp&thumbnail=184x0`" alt="" />
                </div>
                <!-- <span v-if="!playing"></span> -->
            </div>
            <div class="liked">
                <i class="fa fa-heart" aria-hidden="true" ref="like" @click.stop="clickLiked"></i>
                <p v-if="isLiked" :class="{liked: isLiked}">添加喜欢成功~</p>
                <transition name="fade">
                    <p v-if="!isLiked && count > 1">取消喜欢成功~</p>
                </transition>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        song: Object,
        playing: Boolean,
        tmpCurrentTime: Number,
    },
    data() {
        return {
            isLiked: false,
            // isLiked: JSON.parse(localStorage.getItem("isLiked")),
            count: 1,
        };
    },
    methods: {
        clickLiked: function () {
            this.count++;
            if (this.count % 2 === 0) {
                this.isLiked = true;
                this.$refs.like.style.color = 'red';
            } else {
                this.$refs.like.style.color = '';
                this.isLiked = false;
            }
        }
    },
    // watch: {
    //     isLiked: function (isLiked) {
    //         localStorage.setItem("isLiked",JSON.stringify(isLiked));
    //     },
    // }
};
</script>

<style lang="less" scoped>
@keyframes rorate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes opacityLiked {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}


.fade-enter-active {
    animation: opacityLiked 1s linear;
}

.playDisco {
    width: 247rem;
    height: 247rem;
    position: relative;
    margin: 0 auto;

    .needle {
        width: 73rem;
        height: 118rem;
        position: absolute;
        top: -50rem;
        left: 112rem;
        z-index: 9;
        background: url(../assets/needle-ab.png) no-repeat;
        background-size: contain;
        transform-origin: 9rem 11rem;
        transform: rotate(-7deg);
        transition: all .3s;

        &.ndTo {
            transform: rotate(-30deg);
        }
    }

    .turn {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;

        &::before {
            content: " ";
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: 2;
            background: url(../assets/circle1.png) no-repeat;
            background-size: contain;
        }

        .song-rollwrap {
            width: 184rem;
            height: 184rem;
            margin: -92rem 0 0 -92rem;
            position: absolute;
            left: 50%;
            top: 50%;
            z-index: 1;
            animation: rorate 8s linear infinite;
            animation-play-state: paused;

            &.playing {
                animation-play-state: running;
            }

            .songImg {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                overflow: hidden;
            }

            // span {
            //     width: 56rem;
            //     height: 56rem;
            //     position: absolute;
            //     left: 50%;
            //     top: 50%;
            //     margin-top: -28rem;
            //     margin-left: -28rem;
            //     background: url(../assets/play.png) no-repeat;
            //     background-size: contain;
            //     z-index: 10;
            // }
        }

        .liked {
            position: fixed;
            top: 55%;
            right: 30rem;

            i {
                position: absolute;
                top: 0;
                right: 0;
                font-size: 22rem;
                &.active {
                    color: red;
                }
            }

            p {
                margin-top: 30rem;
                padding: 10rem;
                color: rgb(212, 208, 208);
                background-color: rgba(0, 0, 0, 0.511);
                border-radius: 10rem;
                opacity: 0;

                &.liked {
                    animation: opacityLiked 1s linear;
                }

                
            }


        }
    }


}
</style>
