<template>
    <div class="cmt-item">
        <div class="cmt-head">
            <img :src="latestCmt.user.avatarUrl" alt="">
            <img class="author-icon" v-if="latestCmt.user.avatarDetail"
                :src="latestCmt.user.avatarDetail.identityIconUrl" alt="">
        </div>
        <div class="cmt-wrap">
            <!-- 评论用户的信息 -->
            <div class="cmt-header">
                <div class="cmt-meta">
                    <div class="cmt-user">
                        <span class="nickname">{{latestCmt.user.nickname}}</span>
                        <img v-if="latestCmt.user.vipRights?.associator"
                            :src="latestCmt.user.vipRights.associator.iconUrl" alt="">
                    </div>
                    <div class="cmt-time">{{latestCmt.timeStr}}</div>
                </div>
                <div class="cmt-like">
                    <span>{{latestCmt.likedCount>0?latestCmt.likedCount:''}}</span>
                    <i class="fa fa-thumbs-o-up" aria-hidden="true"  @click="clickHander($event)"></i>
                </div>
            </div>
            <!-- 判断是否有回复 -->
            <div class="cmt-content" v-if="repliedContent.length">
                <div class="cmt-box" v-for="(replied,i) in repliedContent" :key="i">回复
                    <span class="active">@{{replied.user.nickname}}</span>
                    <span>：{{latestCmt.content}}</span>
                    <div class="cmt-replied">
                        <span>@{{replied.user.nickname}}：</span>
                        <span>{{replied.content}}</span>
                    </div>
                </div>
            </div>
            <div class="cmt-content" v-else>
                <span>{{latestCmt.content}}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        latestCmt: Object,
    },
    data() {
        return {
            repliedContent: this.latestCmt.beReplied,
        };
    },
    methods: {
        clickHander: function (e) {
            if (e.path[0].style.color == "") {
                e.path[1].childNodes[0].innerText++;
                e.path[1].childNodes[0].style.color = "red";
                e.path[0].style.color = "red";
            }
            else {
                e.path[1].childNodes[0].innerText--;
                if (e.path[1].childNodes[0].innerText == 0) {
                    e.path[1].childNodes[0].innerText = '';
                }
                e.path[1].childNodes[0].style.color = "";
                e.path[0].style.color = "";
            }

        },
    }
};
</script>

<style lang="less" scoped>
.cmt-item {
    padding-top: 10rem;
    width: 100%;
    display: flex;

    .cmt-head {
        height: 35rem;
        margin: 0 10rem;
        position: relative;

        img {
            display: block;
            border-radius: 50%;
            width: 30rem;
            height: 30rem;
        }

        .author-icon {
            position: absolute;
            right: -5rem;
            bottom: 5rem;
            width: 12rem;
            height: 12rem;
        }
    }

    .cmt-wrap {
        width: 325rem;
        padding-right: 10rem;
        padding-bottom: 11rem;
        position: relative;

        .cmt-header {
            display: flex;
            width: 320rem;
            justify-content: space-between;

            .cmt-meta {
                .cmt-user {
                    display: inline-flex;
                    align-items: center;

                    .nickname {
                        font-size: 14rem;
                        color: #666;
                        line-height: 20rem;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }

                    img {
                        display: inline-block;
                        height: 12rem;
                        margin: 0 4rem;
                    }

                }

                .cmt-time {
                    font-size: 9rem;
                    color: #999;
                }
            }

            .cmt-like {
                width: 65rem;
                height: 22rem;
                line-height: 22rem;
                font-size: 11rem;
                color: #999;
                text-align: right;
                position: absolute;
                top: 0;
                right: 10rem;

                i {
                    display: inline-block;
                    font-size: 16rem;
                    margin-left: 6rem;
                    line-height: 0;
                    cursor: pointer;
                }
            }
        }

        .cmt-content {
            color: #333;
            font-size: 15rem;
            line-height: 22rem;
            margin-top: 5rem;
        }

        .cmt-replied {
            margin: 5rem 0;
            padding: 10rem;
            color: #888;
            font-size: 14rem;
            line-height: 21rem;
            border: 1rem solid rgb(236, 232, 232);
        }
    }

    .active {
        color: #507daf;
    }
}
</style>
