<template>
    <div class="songList">
        <h3>歌曲列表</h3>
        <ul>
            <li v-for="song in songL" :key="song.id" class="song-item" @click="$emit('update-song', song)">
                <div class="song">
                    <div class="song-name">{{ song.name }}</div>
                    <div class="song-introduce" v-if="song.song">
                        <div class="sq">
                            <i v-if="song.song.sqMusic!==null"></i>
                            <span v-if="song.song.artists">{{song.song.artists[0].name}}</span>
                            <span v-if="song.song.artists.length>=2"> / {{song.song.artists[1].name}}</span>
                            <span> - {{ song.name }}</span>
                        </div>
                    </div>
                    <div class="song-introduce" v-else>
                        <div class="sq">
                            <i v-if="song.sq!==null"></i>
                            <span v-if="song.art">{{song.ar[0].name}}</span>
                            <span v-if="song.ar.length>=2"> / {{song.ar[1].name}}</span>
                            <span> - {{ song.name }}</span>
                        </div>
                    </div>
                </div>
                <!-- <div class="song-play" v-if="song.id !== songId">
                </div> -->
                <div class="song-play active" v-if="song.id === songId" :class="{playing:playing}">
                    <span>.</span>
                    <span>.</span>
                    <span>.</span>
                    <span>.</span>
                </div>
                <div class="sl-del" @click.stop="deleteSL(song.id)">
                    <svg t="1664448517434" class="icon" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="2965" width="32" height="32">
                        <path
                            d="M507.168 473.232L716.48 263.936a16 16 0 0 1 22.624 0l11.312 11.312a16 16 0 0 1 0 22.624L541.12 507.168 750.4 716.48a16 16 0 0 1 0 22.624l-11.312 11.312a16 16 0 0 1-22.624 0L507.168 541.12 297.872 750.4a16 16 0 0 1-22.624 0l-11.312-11.312a16 16 0 0 1 0-22.624l209.296-209.312-209.296-209.296a16 16 0 0 1 0-22.624l11.312-11.312a16 16 0 0 1 22.624 0l209.296 209.296z"
                            p-id="2966" fill="#707070"></path>
                    </svg>
                </div>
            </li>
        </ul>
        <span class="close" @click="$emit('close-songList')"></span>
    </div>
</template>

<script>


export default {
    props: {
        songlist: Array,
        songId: [Number, String],
        playing: Boolean
    },
    data() {
        return {
            songL: JSON.parse(localStorage.getItem("songL")) || this.songlist
        };
    },
    methods: {
        deleteSL: function (id) {
            this.songL = this.songL.filter(o => o.id !== id);
        }
    },
    watch: {
        songL: function (songL) {
            localStorage.setItem("songL", JSON.stringify(songL))
        }
    }
};
</script>

<style lang="less" scoped>
.songList {
    width: 90%;
    height: 59vh;
    background-color: rgb(248, 246, 246);
    position: fixed;
    // bottom: 60rem;
    bottom: 77rem;
    left: 0;
    z-index: 9999999;
    margin: 20rem;
    overflow: auto;
    border-radius: 10rem;

    h3 {
        font-size: 16rem;
        text-align: center;
        margin: 10rem;
    }

    ul {
        padding: 15rem;

        @keyframes playingrunning {
            from {
                transform: rotateX(15deg);
            }

            to {
                transform: rotateX(60deg);
            }
        }

        .song-item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1rem solid #ccc;
            position: relative;

            .song {
                padding: 6rem;
                margin-left: 30rem;

                .song-name {
                    width: 230rem;
                    font-size: 17rem;
                    padding-bottom: 4rem;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: normal;
                }

                .song-introduce {
                    font-size: 12rem;
                    color: #888;

                    .sq {
                        width: 230rem;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        word-break: normal;
                    }

                    i {
                        display: inline-block;
                        width: 12rem;
                        height: 8rem;
                        margin-right: 4rem;
                        background: url(../assets/index_icon_2x.png) no-repeat;
                        background-size: 166rem 97rem;
                    }
                }
            }

            .song-play {
                padding: 0 15rem;
                width: 22rem;
                height: 22rem;
                background: url(../assets/index_icon_2x.png) no-repeat;
                background-size: 166rem 97rem;
                background-position: -24rem 0;
                position: absolute;
                top: 50%;
                left: 0;
                margin-top: -11rem;

                &.active {
                    background-image: none;
                    display: flex;
                    justify-content: space-around;
                    margin-right: 4rem;

                    span {
                        display: block;
                        width: 4rem;
                        height: 100%;
                        margin: 0 1rem;
                        background-color: red;
                        animation: playingrunning 0.4s infinite alternate;
                        animation-play-state: paused;
                        transform-origin: bottom;

                        &:nth-child(1) {
                            animation-delay: 0s;
                        }

                        &:nth-child(2) {
                            animation-delay: -0.4s;
                        }

                        &:nth-child(3) {
                            animation-delay: -0.2s;
                        }

                        &:nth-child(4) {
                            animation-delay: -0.6s;
                        }
                    }

                    &.playing {
                        span {
                            animation-play-state: running;
                        }
                    }
                }

            }

            .sl-del {
                position: absolute;
                top: 10rem;
                right: 3rem;
            }
        }
    }

    .close {
        position: absolute;
        top: 10rem;
        right: 20rem;
        width: 30rem;
        height: 30rem;
        font-size: 26rem;
        background-image: url(../assets/round_close_light.png);

    }
}
</style>
