<template>
    <div class="m-playlist">
        <i v-if="!loading" class="fa fa-angle-left" aria-hidden="true" @click="$router.back()"></i>
        <!-- loading -->
        <div class="loading" v-if="loading"></div>
        <!-- 歌单 -->
        <section class="pylst-header" v-if="!loading && !showAuthor">
            <div class="plhead-bg"
                :style="`backgroundImage:url(${playlist?.coverImgUrl}?imageView=1&type=webp&thumbnail=126x0);filter:blur(20rem);transform:scale(1.5)`"></div>
            <div class="plhead-wrap">
                <div class="plhead-fl">
                    <img :src="playlist?.coverImgUrl" alt="">
                    <span>歌单</span>
                    <i>{{playlist?.playCount>100000000?(playlist?.playCount/100000000).toFixed(1):(playlist?.playCount/10000).toFixed(1)}}万</i>
                </div>
                <div class="plhead-fr">
                    <h2>{{playlist.name}}</h2>
                    <div class="lsthd-author f-thide"
                        @click="showAuthor = true, getPlayListAuthor(playlist.creator.userId)">
                        <div class="author-pc">
                            <img :src="playlist.creator?.avatarUrl" alt="">
                            <img class="authot-icon" :src="playlist.creator?.avatarDetail?.identityIconUrl" alt="">
                        </div>
                        <p class="authot-name">{{playlist.creator?.nickname}}</p>
                    </div>
                </div>
            </div>
        </section>
        <!-- 歌曲列表 -->
        <div class="pylst-list" v-if="!loading && !showAuthor">
            <h3 class="u-smtitle">歌曲列表</h3>
            <div class="m-sglst">
                <ul>
                    <ListSong v-for="(song,index) in listSongs" :key="song.id" :song="song" :index="index" @click.native="$emit('update-song',song);$emit('update-song-list',listSongs)" :playing="playing" :songId="songId"></ListSong>
                </ul>
                <!-- <div class="more-wrap">
                    <span class="moreSongs" @click="moreSongs;">查看更多歌曲</span>
                </div> -->
            </div>
        </div>
        <!-- 评论 -->
        <!-- 精彩评论 -->
        <div class="m-comment" v-if="!loading && !showAuthor">
            <div class="m-talk">
                <h4 class="cmt-title">精彩评论</h4>
                <div class="cmt-list">
                    <ListComment v-for="hotCmt in Comments.hotComments" :key="hotCmt.commentId" :hotCmt="hotCmt">
                    </ListComment>
                </div>
            </div>
        </div>
        <!-- 最新评论 -->
        <div class="m-comment" v-if="!loading && Comments.comments && !showAuthor">
            <div class="m-talk">
                <h4 class="cmt-title">最新评论({{Comments.total}})</h4>
                <div class="cmt-list">
                    <LatestComments v-for="latestCmt in Comments.comments" :key="latestCmt.commentId"
                        :latestCmt="latestCmt"></LatestComments>
                </div>
            </div>
        </div>
        <!-- 歌单作者信息 -->
        <div class="show-author" v-if="showAuthor">
            <i class="fa fa-angle-left" aria-hidden="true" @click="showAuthor = false" v-if="showAuthor"></i>
            <ShowAuthor :authorInfo="authorInfo" :authorPlayList="authorPlayList"></ShowAuthor>
        </div>
    </div>
</template>

<script>
import ListSong from '@/components/ListSong.vue';
import ListComment from '@/components/ListComment.vue';
import LatestComments from '@/components/LatestComments.vue';
import ShowAuthor from '@/components/ShowAuthor.vue';
export default {
    props: {
        playing: Boolean,
        songId: Number,
    },
    components: {
        ListSong,
        ListComment,
        LatestComments,
        ShowAuthor
    },
    data() {
        return {
            playlist: [],
            listSongs: [],
            limitNum: 10,
            loading: false,
            Comments: {},
            showAuthor: false,
            authorInfo: {},
            authorPlayList: [],
        };
    },
    methods: {
        getPlayList: function (id) {
            this.loading = true;
            this.axios
                .get("https://apis.netstart.cn/music/playlist/detail", {
                    params: { id },
                })
                .then((res) => {
                    this.loading = false;
                    this.playlist = res.data.playlist;
                });
        },
        playListSongs: function (id) {
            this.loading = true;
            this.axios.get("https://apis.netstart.cn/music/playlist/track/all", {
                params: { id, limit: this.limitNum, offset: 0 },
            }).then(res => {
                // console.log(res);
                this.loading = false;
                this.listSongs = res.data.songs;
            });
        },
        // moreSongs: function () {
        //     this.limitNum += 10;
        //     // console.log(123);
        // },
        hotCommentsList: function (id) {
            this.axios.get("https://apis.netstart.cn/music/comment/playlist", {
                params: { id }
            }).then(res => {
                // console.log(res.data.hotComments);
                this.Comments = res.data;
            });
        },

        getPlayListAuthor: function (uid) {
            this.axios.get("https://apis.netstart.cn/music/user/detail", { params: { uid } }).then(res => {
                // console.log(res.data);
                this.authorInfo = res.data;
            });
            this.axios.get("https://apis.netstart.cn/music/user/playlist", {params: {uid}}).then(res => {
                // console.log(res.data);
                this.authorPlayList = res.data.playlist;
            });
        }

    },
    created: function () {
        this.getPlayList(this.$route.query.id);
        this.playListSongs(this.$route.query.id);
        this.hotCommentsList(this.$route.query.id);
    },
    beforeRouteUpdate(to, from, next) {
        this.getPlayList(to.query.id);
        this.playListSongs(to.query.id);
        this.hotCommentsList(to.query.id);
        next();
    },
};
</script>

<style lang="less" scoped>
.m-playlist {
    background-color: #fff;
    min-height: 100%;

    .fa-angle-left {
        position: absolute;
        top: 2rem;
        left: 4rem;
        font-size: 26rem;
        z-index: 9;
        color: white;
    }

    .pylst-header {
        position: relative;
        padding: 30rem 10rem 30rem 15rem;
        overflow: hidden;

        .plhead-bg {
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: 50%;
        }

        .plhead-wrap {
            display: flex;
            position: relative;
            z-index: 2;

            .plhead-fl {
                width: 126rem;
                height: 126rem;
                position: relative;
                background-color: #e2e2e3;

                img {
                    display: block;
                    width: 100%;
                    height: 100%;
                }

                span {
                    position: absolute;
                    z-index: 3;
                    top: 10rem;
                    left: 0;
                    padding: 0 8rem;
                    height: 17rem;
                    color: #fff;
                    font-size: 9rem;
                    text-align: center;
                    line-height: 17rem;
                    background-color: rgba(217, 48, 48, .8);
                    border-top-right-radius: 17rem;
                    border-bottom-right-radius: 17rem;
                }

                i {
                    position: absolute;
                    right: 5rem;
                    top: 2rem;
                    z-index: 3;
                    padding-left: 15rem;
                    color: #fff;
                    font-size: 12rem;
                    background-position: 0;
                    background-repeat: no-repeat;
                    background-size: 11rem 10rem;
                    text-shadow: 1rem 0 0 rgb(0 0 0 / 15%);
                    background-image: url(../assets/listening.svg);
                    font-style: normal;
                }
            }

            .plhead-fr {
                margin-left: 16rem;
                padding-top: 3rem;

                h2 {
                    padding-top: 1rem;
                    font-size: 17rem;
                    line-height: 1.3;
                    color: #fefefe;
                    width: 208rem;
                    height: 44rem;
                }

                .lsthd-author {
                    margin-top: 5rem;
                    display: flex;
                    align-items: center;

                    .author-pc {
                        position: relative;

                        img {
                            // display: block;
                            width: 30rem;
                            height: 30rem;
                            border-radius: 50%;
                            background-size: cover;
                            background-position: center;
                        }

                        .authot-icon {
                            position: absolute;
                            right: -5rem;
                            bottom: 0;
                            width: 12rem;
                            height: 12rem;
                        }
                    }


                    .authot-name {
                        color: hsla(0, 0%, 100%, .7);
                        font-size: 14rem;
                        padding-left: 4rem;
                        padding-bottom: 5rem;
                    }
                }
            }
        }
    }

    .u-smtitle {
        height: 23rem;
        line-height: 23rem;
        padding: 0 10rem;
        font-size: 12rem;
        color: #666;
        background-color: #eeeff0;
    }

    .more-wrap {
        position: absolute;
        z-index: 10;
        left: 0;
        right: 0;
        bottom: -120rem;

        .moreSongs {
            display: block;
            margin-top: 137rem;
            color: #999;
            font-size: 14rem;
            text-align: center;
        }
    }

    .loading {
        width: 50rem;
        height: 50rem;
        background: url(../assets/loading.gif) no-repeat;
        background-size: cover;
        position: fixed;
        top: 50%;
        left: 50%;
        margin-left: -25rem;
        margin-top: -25rem;
    }

    .m-comment {
        position: relative;
        margin-top: 18px;

        .m-talk {
            margin-top: -20px;

            .cmt-title {
                padding: 0 10px;
                height: 23px;
                line-height: 23px;
                color: #666;
                font-size: 12px;
                font-weight: 400;
                background: rgba(0, 0, 0, .05);
            }
        }
    }

    .show-author {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: white;
        z-index: 9;

        .fa-angle-left {
            position: absolute;
            top: 10rem;
            left: 10rem;
            font-size: 26rem;
        }
    }
}



