<template>
    <li class="home-card" @click="$router.push({path:'/playlist',query:{id:item.id}})">
        <img :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=370x0`" alt="">
        <p class="remd_text">{{ item.name }}</p>
        <span class="count">{{count}}</span>
    </li>
</template>

<script>
export default {
    props: {
        item: Object,
    },
    data() {
        return {
            count: this.item.playCount > 100000000 ? (this.item.playCount / 100000000).toFixed(1) : (this.item.playCount / 10000).toFixed(1) + '万',
        };
    },
    components: {

    },


};
</script>

<style lang="less" scoped>
li.home-card {
    width: 30%;
    flex-grow: 1;
    margin-bottom: 15rem;
    position: relative;

    &:nth-child(3n + 2) {
        margin: 0 3rem;
    }

    &::after {
        content: " ";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 20px;
        z-index: 2;
        background-image: linear-gradient(180deg, rgba(0, 0, 0, .2), rgba(0, 0, 0, 0));
    }

    .remd_text {
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        padding: 6rem 2rem 0 6rem;
        min-height: 30rem;
        line-height: 1.2;
        font-size: 13rem;
    }

    .count {
        position: absolute;
        top: 2rem;
        right: 5rem;
        color: white;
        padding-left: 13rem;
        font-size: 12rem;
        background-image: url(../assets/listening.svg);
        background-position: 0;
        background-repeat: no-repeat;
        background-size: 11rem 10rem;
    }
}
</style>
