<template>
    <div class="playbar" v-if="song">
        <img v-if="song.picUrl" :class="{rotate: playing}" :src="`${song?.picUrl}?imageView=1&type=webp&thumbnail=60x0`" alt="">
        <img v-else :class="{rotate: playing}" :src="`${song.al?.picUrl}?imageView=1&type=webp&thumbnail=60x0`" alt="" />
        <div class="name" v-if="song.song">
            <h3>{{song?.name}}</h3>
            <span>{{song.song.artists[0].name}}</span>
            <span v-if="song.song.artists[1]"> / {{song.song.artists[1].name}}</span>
        </div>
        <div class="name" v-else>
            <h3>{{song?.name}}</h3>
            <span>{{song.ar[0].name}}</span>
            <span v-if="song.ar[1]"> / {{song.ar[1].name}}</span>
        </div>
        <div class="time">
            <span>{{parseInt(currentTime/60%60)>9?parseInt(currentTime/60%60):'0'+parseInt(currentTime/60%60).toFixed(0)}}
                :
                {{parseInt(currentTime%60)>9?parseInt(currentTime%60):'0'+parseInt(currentTime%60)}}
                / {{parseInt(duration/60%60)>9?parseInt(duration/60%60):'0'+parseInt(duration/60%60)}} :
                {{parseInt(duration%60)>9?parseInt(duration%60):'0'+parseInt(duration%60)}}</span>
        </div>
        <div class="isPlaying">
            <i class="fa fa-play" aria-hidden="true" v-if="!playing" @click.stop="$emit('song-play')"></i>
            <i class="fa fa-pause" aria-hidden="true" v-else @click.stop="$emit('song-pause')"></i>
            <canvas ref="canvas" width="30rem" height="30rem"></canvas>
        </div>
        <div class="forward">
            <i class="fa fa-step-forward" aria-hidden="true" 
            @click.stop="$emit('change-song', 'next')"></i>
        </div>
        <div class="models">
            <!-- <button @click.stop="$emit('toggle-showSongList')">列</button> -->
            <i class="fa fa-bars" aria-hidden="true" @click.stop="$emit('toggle-showSongList')"></i>
        </div>
    </div>
</template>

<script>
// import { watch } from 'vue';

export default {
    props: {
        song: Object,
        playing: Boolean,
        duration: Number,
        currentTime: Number,
    },
    data() {
        return {

        };
    },
    computed: {
        songCurrentTime: function () {
            return this.currentTime / this.duration;
        }
    },

    watch: {
        songCurrentTime: function (n) {
            // console.log(n);
            var canvas = this.$refs.canvas;
            var context = canvas.getContext('2d');
            context.clearRect(0, 0, 40, 40);
            context.beginPath();
            context.arc(15, 15, 15, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
            context.stroke();
            context.lineWidth=5;
            context.strokeStyle = 'red';
        },
        
    }
};
</script>

<style lang="less" scoped>
@keyframes rotateImg {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.playbar {
    width: 100%;
    height: 60rem;
    background-color: rgb(0, 0, 0);
    // background-color: white;
    position: fixed;
    bottom: -1rem;
    left: 0;
    z-index: 9999;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10rem;
    color: white;
    // color: black;

    img {
        border-radius: 50%;
        width: 40rem;
        height: 40rem;
        animation: rotateImg 6s linear infinite;
        animation-play-state: paused;

        &.rotate {
            animation-play-state: running;
        }
    }

    .name {
        width: 100rem;
        margin-left: 10rem;

        h3 {
            width: 200rem;
            font-size: 14rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            word-break: normal;
        }

        span {
            color: #999;
        }
    }

    .time {
        widows: 100rem;
        margin-top: 30rem;
    }

    .isPlaying {
        width: 40rem;
        height: 40rem;
        position: relative;
        font-size: 16rem;
        display: flex;
        margin-left: 10rem;
        margin-right: 10rem;
        color: red;

        i {
            position: absolute;
            top: 11rem;
            left: 13rem;
        }

        .fa-play {
                left: 15rem;
            } 

        canvas {
            border-radius: 50%;
            // box-shadow: 0 0 0 1rem white;
        }
    }

    .forward {
        color: red;
        font-size: 18rem;
        vertical-align: middle;
        margin-right: 20rem;
    }

    .models {
        font-size: 18rem;
        color: red;
    }
}
</style>
