<template>
    <li class="song-item" @click="$emit('update-song', song)">
        <div class="song">
            <div class="song-name">{{ song.name }}</div>
            <div class="song-introduce">
                <div class="sq">
                    <i v-if="song.song.sqMusic!==null"></i>
                    <span>{{songAuthor[0].name}}</span>
                    <span v-if="songAuthor.length>=2"> / {{songAuthor[1].name}}</span>
                    <span> - {{ song.name }}</span>
                </div>
            </div>
        </div>
        <div class="song-play" v-if="song.id !== songId">
            <!-- <span></span> -->
        </div>
        <div class="song-play active" v-else :class="{playing:playing}">
            <span>.</span>
            <span>.</span>
            <span>.</span>
            <span>.</span>
        </div>
    </li>
</template>

<script>
export default {
    props: {
        song: Object,
        songId: [Number, String],
        playing: Boolean,
    },
    data() {
        return {
            songAuthor: this.song.song.artists,
        };
    },

};
</script>

<style lang="less" scoped>
@keyframes playingrunning {
    from {
        transform: rotateX(15deg);
    }

    to {
        transform: rotateX(60deg);
    }
}

.song-item {
    margin-left: 10rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1rem solid #ccc;
    position: relative;

    .song {
        padding: 6rem;

        .song-name {
            width: 310rem;
            font-size: 17rem;
            padding-bottom: 4rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            word-break: normal;
        }

        .song-introduce {
            font-size: 12rem;
            color: #888;

            .sq {
                width: 310rem;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                word-break: normal;
            }

            i {
                display: inline-block;
                width: 12rem;
                height: 8rem;
                margin-right: 4rem;
                background: url(../assets/index_icon_2x.png) no-repeat;
                background-size: 166rem 97rem;
            }
        }
    }

    .song-play {
        padding: 0 15rem;
        width: 22rem;
        height: 22rem;
        background: url(../assets/index_icon_2x.png) no-repeat;
        background-size: 166rem 97rem;
        background-position: -24rem 0;

        &.active {
            background-image: none;
            display: flex;
            justify-content: space-around;

            span {
                display: block;
                width: 4rem;
                height: 100%;
                margin: 0 1rem;
                background-color: red;
                animation: playingrunning 0.4s infinite alternate;
                animation-play-state: paused;
                transform-origin: bottom;

                &:nth-child(1) {
                    animation-delay: 0s;
                }

                &:nth-child(2) {
                    animation-delay: -0.4s;
                }

                &:nth-child(3) {
                    animation-delay: -0.2s;
                }

                &:nth-child(4) {
                    animation-delay: -0.6s;
                }
            }

            &.playing {
                span {
                    animation-play-state: running;
                }
            }
        }

    }

}
</style>
