<template>
  <div id="app">
    <div class="box" v-if="$route.meta.showNav">
      <div class="topbar">
        <div class="topfix">
          <div class="logo">
            <i></i>
            <span>网易云音乐</span>
          </div>
          <div class="installBtn">
            <span>下载APP</span>
          </div>
        </div>
      </div>
      <nav>
        <router-link to="/">
          <span>推荐音乐</span>
        </router-link>
        <router-link to="/hot"><span>热歌榜</span></router-link>
        <router-link to="/search"><span>搜索</span></router-link>
      </nav>
    </div>
    <router-view @update-song="song = $event" :songId="song?.id" :playing="playing"
      @update-song-list="songlist = $event" :style="{ marginBottom: song ? '60rem' : '0' }" />

    <audio :src="songSrc" autoplay ref="audio" @play="playing=true" @pause="playing=false"
      @durationchange="duration = $event.target.duration" @timeupdate="currentTime = $event.target.currentTime"
      @ended="changeSongs('next')" @volumechange="volume = $event.target.volume"></audio>

    <PlayBar :song="song" :playing="playing" @song-play="$refs.audio.play()" @song-pause="$refs.audio.pause()"
      :duration="duration" :currentTime="currentTime" @click.native="showPlayPage = true"
      @toggle-showSongList="showSongList = !showSongList" :songIds="songIds" @change-song="changeSongs($event)">
    </PlayBar>

    <PlayPage v-if="song && showPlayPage" :song="song" :playing="playing" @song-play="$refs.audio.play()"
      @song-pause="$refs.audio.pause()" :duration="duration" :currentTime="currentTime"
      @update-time="$refs.audio.currentTime = $event" @toggle-showSongList="showSongList = !showSongList" :model="model"
      :models="models" @change-song="changeSongs($event)" @change-model="changeModel" :songIds="songIds"
      :volume="volume" @update-volume="$refs.audio.volume = $event">
    </PlayPage>

    <transition name="ani-songlist">
      <SongList v-show="showSongList" :songlist="songlist" :songId="song?.id" @update-song="song = $event"
        @close-songList="showSongList = false" :playing="playing">
      </SongList>
    </transition>
  </div>
</template>

<script>
import PlayBar from '@/components/PlayBar.vue';
import PlayPage from '@/components/PlayPage.vue';
import SongList from '@/components/SongList.vue';
// import { watch } from 'vue';
export default {
  components: {
    PlayBar,
    PlayPage,
    SongList
  },
  data: function () {
    return {
      song: JSON.parse(localStorage.getItem('song')),
      playing: false,
      duration: 0,
      currentTime: Number(localStorage.getItem("currentTime")) || 0,
      showPlayPage: false,
      songlist: JSON.parse(localStorage.getItem('songlist')) || [],
      showSongList: false,
      models: ["顺序播放", "随机播放", "单曲循环"],
      model: "顺序播放",
      songIds: null,
      volume: Number(localStorage.getItem("volume")) || 0,
    };
  },
  mounted() {
    this.$refs.audio.currentTime = this.currentTime;
    this.$refs.audio.volume = this.volume;
  },
  computed: {
    songSrc: function () {
      return this.song ? `https://music.163.com/song/media/outer/url?id=${this.song?.id || this.song.al?.id}.mp3` : '';
    }
  },

  methods: {
    changeSongs: function (direction) {
      // console.log(direction);
      switch (this.model) {
        case '顺序播放':
          var currentSongIndex = this.songlist.findIndex(
            (o) => o.id === this.song.id
          );
          if (direction === "next") {
            this.song =
              currentSongIndex === this.songlist.length - 1
                ? this.songlist[0]
                : this.songlist[currentSongIndex + 1];
            this.songIds = this.song.id;
          } else {
            this.song =
              currentSongIndex === 0
                ? this.songlist[this.songlist.length - 1]
                : this.songlist[currentSongIndex - 1];
            this.songIds = this.song.id;
          }
          break;

        case '随机播放':
          var choose = this.songlist.filter((o) => o.id !== this.song.id);
          this.song = choose[Math.floor(Math.random() * choose.length)];
          this.songIds = this.song.id;
          break;


        case "单曲循环":
          this.$refs.audio.currentTime = 0;
          this.$refs.audio.play();
          break;
      }
    },

    changeModel: function () {
      var index = this.models.findIndex((o) => o === this.model);

      this.model =
        index === this.models.length - 1
          ? this.models[0]
          : this.models[index + 1];
    },

  },

  watch: {
    song: function (song) {
      localStorage.setItem('song', JSON.stringify(song));
    },
    songlist: function (songlist) {
      localStorage.setItem('songlist', JSON.stringify(songlist));
    },
    currentTime: function (currentTime) {
      localStorage.setItem('currentTime', JSON.stringify(currentTime));
    },
    volume: function (volume) {
      localStorage.setItem('volume', JSON.stringify(volume));
    },
  }

  // created: function () {
  //   console.log("App this.$route", this.$route);
  // },
};
</script>

