<template>
    <div class="playLyric">
        <ul v-if="songLyric.length" :style="`transform: translateY(${findCurrentLyric.index* -35+200}rem);transition: all .6s;`">
            <!-- <li v-for="(lrc,i) in songLyric" :key="i" :class="{active: findCurrentLyric.index===i}">{{lrc?.text}}
            </li> -->
            <li v-for="(lrc,i) in songLyric" :key="i" :class="{active: findCurrentLyric.index===i}" :style="`animation-duration:${findCurrentLyric.time+0.5}s;`">{{lrc?.text}}
            </li>
        </ul>
        <h3 v-else>暂无歌词</h3>
    </div>
</template>

<script>


export default {
    props: {
        playing: Boolean,
        songLyric: Array,
        tmpCurrentTime: Number,
    },
    computed: {
        findCurrentLyric: function () {
            // console.log(this.songLyric);
            const copy = [...this.songLyric].reverse();
            console.log(copy);
            const index = copy.findIndex((o) => o.time < this.tmpCurrentTime);
            const lyricTime = copy[index]?.time - copy[index + 1]?.time;
            return {
                index: copy.length - 1 - index,
                text: copy[index]?.text,
                time: lyricTime,
            };
        },
    },
};
</script>

<style lang="less" scoped>
@keyframes scan {
    0% {
        background-size: 0 100%;
    }

    100% {
        background-size: 100% 100%;
    }
}

.playLyric {
    width: 100%;
    height: 400rem;
    overflow: auto;
    font-size: 16rem;

    ul {
        margin-left: 20rem;
        transform: translateY(200rem);
        font-size: 14rem;
        text-align: center;
        li {
            height: 35rem;
            line-height: 30rem;
            background: white -webkit-linear-gradient(left, rgb(189, 105, 225), rgb(71, 147, 214)) no-repeat 0 0;
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-size: 0 100%;

            &.active {
                background-size: 100% 100%;
                animation: scan linear;
            }
        }
    }
}

h3 {
    width: 100%;
    text-align: center;
    margin-top: 200rem;
}
</style>
